<template>
  <view>
    <!-- 搜索框 -->
    <view class="search-box">
      <uni-search-bar :focus="true" v-model="searchValue" @input="input" :radius="100" cancelButton="none"></uni-search-bar>
    </view>
    
    <!-- 搜索建议列表 -->
    <view class="sugg-list" v-if="searchResults.length !== 0">
      <view class="sugg-item" v-for="(item, i) in searchResults" :key="i" @click="gotoDetail(item.goods_id)">
        <view class="goods-name">{{item.goods_name}}</view>
        <uni-icons type="arrowright" size="16"></uni-icons>
      </view>
    </view>
    
    <!-- 搜索历史 -->
    <view class="history-box" v-else>
      <!-- 标题区域 -->
      <view class="history-title">
        <text>搜索历史</text>
        <uni-icons type="trash" size="17" @click="cleanHistory"></uni-icons>
      </view>
      <!-- 列表区域 -->
      <view class="history-list">
        <uni-tag :text="item" v-for="(item, i) in historys" :key="i" @click="gotoGoodsList(item)"></uni-tag>
      </view>
    </view>
  </view>
  
</template>

<script>
  export default {
    data() {
      return {
        // 表单value值
        searchValue:'',
        // 防抖timer
        timer:null,
        // 搜索结果列表
        searchResults: [],
        // 搜索关键词的历史记录
            historyList: []
      };
    },
    methods: {
      input(e) {
        clearTimeout(this.timer)
        this.timer=setTimeout(()=>{
          // console.log(e)
          // 根据关键词，查询搜索建议列表
            this.getSearchList()
        },1000)
      },
      // 根据搜索关键词，搜索商品建议列表
      async getSearchList() {
        // 判断关键词是否为空
        if (this.searchValue === '') {
          this.searchResults = []
          return
        }
        // 发起请求，获取搜索建议列表
        const { data: res } = await uni.$http.get('/api/public/v1/goods/qsearch', { query: this.searchValue })
        if (res.meta.status !== 200) return uni.$showMsg()
        this.searchResults = res.message
        this.saveSearchHistory()
      },
      // 2. 保存搜索关键词的方法
        saveSearchHistory() {
          // 2.1 直接把搜索关键词 push 到 historyList 数组中
          this.historyList.push(this.searchValue)
           // 1. 将 Array 数组转化为 Set 对象
            const set = new Set(this.historyList)
            // 2. 调用 Set 对象的 delete 方法，移除对应的元素
            set.delete(this.searchValue)
            // 3. 调用 Set 对象的 add 方法，向 Set 中添加元素
            set.add(this.searchValue)
            // 4. 将 Set 对象转化为 Array 数组
            this.historyList = Array.from(set)
            // 调用 uni.setStorageSync(key, value) 将搜索历史记录持久化存储到本地
            uni.setStorageSync('kw', JSON.stringify(this.historyList))
        },
      // 点击搜索建议的 Item 项，跳转到商品详情页面：
      gotoDetail(goods_id) {
        uni.navigateTo({
          // 指定详情页面的 URL 地址，并传递 goods_id 参数
          url: '/package1/goods_detail/goods_detail?goods_id=' + goods_id
        })
      },
      // 清空搜索历史记录
      cleanHistory() {
        // 清空 data 中保存的搜索历史
        this.historyList = []
        // 清空本地存储中记录的搜索历史
        uni.setStorageSync('kw', '[]')
      },
      // 点击跳转到商品列表页面
      gotoGoodsList(kw) {
        uni.navigateTo({
          url: '/package1/goods_list/goods_list?query=' + kw
        })
      }
    },
    onLoad() {
      this.historyList = JSON.parse(uni.getStorageSync('kw') || '[]')
    },
    computed:{
      historys(){
        return [...this.historyList].reverse()
      }
    }
  }
</script>

<style lang="scss">
.search-box {
  // 设置定位效果为“吸顶”
  position: sticky;
  // 吸顶的“位置”
  top: 0;
  // 提高层级，防止被轮播图覆盖
  z-index: 999;
}
.sugg-list {
  padding: 0 5px;

  .sugg-item {
    font-size: 12px;
    padding: 13px 0;
    border-bottom: 1px solid #efefef;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .goods-name {
      // 文字不允许换行（单行文本）
      white-space: nowrap;
      // 溢出部分隐藏
      overflow: hidden;
      // 文本溢出后，使用 ... 代替
      text-overflow: ellipsis;
      margin-right: 3px;
    }
  }
}
.history-box {
  padding: 0 5px;

  .history-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    font-size: 13px;
    border-bottom: 1px solid #efefef;
  }

  .history-list {
    display: flex;
    flex-wrap: wrap;

    .uni-tag {
      margin-top: 5px;
      margin-right: 5px;
    }
  }
}
</style>
